#wrapper{
  /* border: gray solid 1px; */
  width: 0 auto;
  margin: 0 auto 0 auto;
  overflow: hidden;
  background-color: rgb(255, 255, 255);
  color: black;
  font-family: Arial;
  text-align: center;
  border-radius: 10px;
  padding-bottom: 30px;
}
#logo{
width: 80%;
border-radius: 10px;
}
button{
outline: 0;
transition-duration: 0.4s;
position: relative;
background-color: #39b4bd; /* Green */
border: none;
color: black;
padding: 15px 20px;
text-align: center;
text-decoration: none;
display: inline-block;
font-size: 16px;
margin: 4px 4px;
cursor: pointer;
border-radius: 10px;
}
button:hover {
  background-color: #39b4bd; /* Green */
  color: white;
}
.button1 {
  background-color: white; 
  color: black; 
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.button2 {
  background-color: white; 
  color: black; 
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
footer{
  padding: auto 0;  
  height: 30px;
  clear: both; /* keep the 3rd sub same position*/
  font-size: .8em;
  
}
footer ul{
  
  width: 400px;
  margin: auto;
  padding-top:50px ;
}
footer li{ 
  padding-right: 20px;
  float: left;
  list-style-type: none;
  
}
